// src/app/blog/page.tsx

import BlogPreview from "@/components/Blog/BlogPreview";
import BlogCategoryFilter from "@/components/Blog/BlogCategoryFilter";

async function fetchBlogs(category?: string) {
  const url = category
    ? `${process.env.NEXT_PUBLIC_API_BASE_URL}/api/blogs?category=${category}`
    : `${process.env.NEXT_PUBLIC_API_BASE_URL}/api/blogs`;

  const res = await fetch(url, { cache: "no-store" });
  return res.json();
}

export default async function BlogPage({ searchParams }: { searchParams: { category?: string } }) {
  const category = searchParams?.category || "";
  const blogs = await fetchBlogs(category);

  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-4">Community Blogs</h1>
      <BlogCategoryFilter selected={category} />
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4 mt-6">
        {blogs.length ? (
          blogs.map((blog: any) => <BlogPreview key={blog.id} blog={blog} />)
        ) : (
          <p className="text-gray-500">No blogs found.</p>
        )}
      </div>
    </div>
  );
}
